.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 999;
    transition: opacity 0.5s ease-in-out;

    &.fade {
        opacity: 1;

        .content {
            transform: scale(1);
        }
    }

    .content {
        transform: scale(0.4);
        transition: transform 0.5s ease-in-out;
    }

    .bg {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
        position: absolute;
        z-index: -1;
    }
}
